import React from 'react'
import avatar1 from './imgs/avatar-aden.jpg'
import avatar2 from './imgs/avatar-aiysha.jpg'
import avatar3 from './imgs/avatar-arthur.jpg'
import iconQuotes from './imgs/icon-quotes.svg'
import bgPattern from './imgs/bg-pattern.svg'
import bgPatternHome from './imgs/bg-pattern-home-5.svg'
function DeliverySection() {
  return (
    <div className=" flex flex-col">
      <img className="bg-pattern" src={bgPattern}/>
      <img className="bg-pattern-home" src={bgPatternHome}/>

     <div className="delivery-section container flex flex-col">
     <span className="main-title text-center">Delivering real results for top companies. </span>
     <span className="main-title text-center">Some of our <span className="green-text">success stories.</span></span> 
      <div className="details grid">
        <div className="info ">
          <img className="quotes" src={iconQuotes}/>
          <p> “The team perfectly fit the specialized skill set required. They focused on the most essential features helping us launch the platform eight months faster than planned.”</p>
          <span className="name">Kady Baker</span>
          <span className="job">Product Manager at Bookmark</span>
           <img  className="avatar" src={avatar1}/>
        </div>
        <div className="info ">
        <img className="quotes" src={iconQuotes}/>
          <p> “We needed to automate our entire onboarding process. The team came in and built out the whole journey. Since going live, user retention has gone through the roof!”</p>
          <span className="name">Aiysha Reese</span>
          <span className="job">Founder of Manage</span>
          <img className="avatar" src={avatar2}/>
        </div>
        <div className="info ">
        <img className="quotes" src={iconQuotes}/>
          <p> “Amazing. Our team helped us build an app that delivered a new experience for hiring a physio. The launch was an instant success with 100k downloads in the first month.”</p>
          <span className="name">Arthur Clarke</span>
          <span className="job">Co-founder of MyPhysio</span>
          <img className="avatar" src={avatar3}/>
        </div>
      </div>
     </div>
    
    </div>
  );
}

export default DeliverySection;

